import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Button from '@material-ui/core/Button';
import MenuDrawer from './MenuDrawer';
import MenuIcon from '@material-ui/icons/Menu';
import IconButton from '@material-ui/core/IconButton';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    appBar: {
        height: 65,
        backgroundColor: 'transparent',
        display: 'flex',
        alignItems: 'flex-end',
        justifyContent: 'center',
        position: 'absolute',
        right: 0,
    },
    itemBar: {
        backgroundColor: 'transparent',
        padding: '6px 10px',
        margin: 5,
        textTransform: 'initial',
        fontWeight: '600',
        color: 'white',
        fontSize: 'medium',
        '&:hover': {
            backgroundColor: 'transparent',
        }
    },
    phoneToolBar: {
        [theme.breakpoints.down('sm')]: {
            display: 'block !important'
         
        },
    },
    normalToolBar: {
        [theme.breakpoints.down('sm')]: {
            display: 'none'
        },
    },
    icon: {
        fontSize: 'xx-large'
    }
}));

export default function DenseAppBar() {
    const classes = useStyles();

    const [open, setOpen] = React.useState(false);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    return (
        <div className={classes.root}>
            <AppBar elevation={0} className={classes.appBar} position="static">
                <Toolbar variant="dense">
                    <div className={classes.normalToolBar}>
                        <Button variant="contained" href="#home" className={classes.itemBar} disableElevation>
                            Home
                        </Button>
                        <Button variant="contained" href="#about" className={classes.itemBar} disableElevation>
                            About
                        </Button>
                        <Button variant="contained" href="#proyects" className={classes.itemBar} disableElevation>
                            Proyects
                        </Button>
                        <Button variant="contained" href="#contact" className={classes.itemBar} disableElevation>
                            Contact
                        </Button>
                    </div>
                    <div className={classes.phoneToolBar} style={{display: 'none'}}>
                        <IconButton
                            color="inherit"
                            aria-label="open drawer"
                            edge="end"
                            onClick={handleDrawerOpen}
                            fontSize="medium"
                            
                        >
                            <MenuIcon className={classes.icon} />
                        </IconButton>
                    </div>
                </Toolbar>
            </AppBar>
            <MenuDrawer open={open} handleDrawerOpen={handleDrawerOpen} handleDrawerClose={handleDrawerClose} />
        </div>
    );
}